<template>
  <div class="partner-company">
    <el-container>
      <el-header class="header">
        <!-- 搜索表单 start -->
        <el-form
          :inline="true"
          :model="searchValidateForm"
          ref="searchValidateForm"
          label-width="100px"
          class="search-validate-form"
          :rules="searchValidateFormRules"
        >
          <!-- 请选择公司 start -->
          <el-form-item label="" prop="companyId">
            <el-select
              v-model="searchValidateForm.companyId"
              filterable
              placeholder="请选择公司"
              @change="onChangeSearchCompany"
              clearable
            >
              <el-option
                v-for="(item, index) in searchCompanyOptions"
                :key="index"
                :label="item.COMPANY_NAME"
                :value="item.COMPANY_ID"
              >
              </el-option>
            </el-select>
          </el-form-item>
          <!-- 请选择公司 end -->

          <!-- 请选择景区 start -->
          <el-form-item label="" prop="spotId">
            <el-select
              v-model="searchValidateForm.spotId"
              filterable
              placeholder="请选择景区"
              @change="onChangeSearchSpot"
              clearable
            >
              <el-option
                v-for="(item, index) in searchScenicOptions"
                :key="index"
                :label="item.SCENIC_SPOT_NAME"
                :value="item.SCENIC_SPOT_ID"
              >
              </el-option>
            </el-select>
          </el-form-item>
          <!-- 请选择景区 end -->
		  
		  <!-- 请选择景区 start -->
		  <el-form-item label="" prop="huntSwitch">
		    <el-select
		      v-model="searchValidateForm.huntSwitch"
		      filterable
		      placeholder="请选择寻宝活动状态"
		      @change="onChangeSearchhuntSwitch"
		      clearable
		    >
		      <el-option
		        v-for="(item, index) in huntSwitchOptions"
		        :key="index"
		        :label="item.label"
		        :value="item.value"
		      >
		      </el-option>
		    </el-select>
		  </el-form-item>
		  <!-- 请选择景区 end -->

          <!-- 请选择日期统计方式 start -->
          <el-form-item label="" prop="selectDateState">
            <el-select
              v-model="searchValidateForm.selectDateState"
              filterable
              placeholder="请选择日期统计方式"
              @change="onDateChange"
            >
              <el-option
                v-for="(item, index) in dateStateOptions"
                :key="index"
                :label="item.label"
                :value="item.value"
              >
              </el-option>
            </el-select>
          </el-form-item>
          <!-- 请选择日期统计方式 end -->

          <!-- 输入起止时间 start -->
          <el-form-item label="" prop="startEndDate" style="width: 350px;">
            <el-date-picker
              v-model="searchValidateForm.startEndDate"
              :type="dateType"
              range-separator="至"
              start-placeholder="开始日期"
              end-placeholder="结束日期"
			  :append-to-body='false'
            >
            </el-date-picker>
          </el-form-item>
          <!-- 输入起止时间 end -->

        </el-form>
        <!-- 搜索表单 end -->

        <!-- 操作按钮 start -->
        <div>
          <!-- 重置 start -->
          <el-button
            type="primary"
            icon="el-icon-refresh-right"
            @click="onReset"
            >重置</el-button
          >
          <!-- 重置 end -->

          <!-- 查询数据 start -->
          <el-button
            type="primary"
            icon="el-icon-search"
            @click="onSearch('searchValidateForm')"
            >查询数据</el-button
          >
          <!-- 查询数据 end -->

          <!-- 导出 Excel start -->
          <el-button
            type="primary"
            :loading="downloadLoading"
            icon="el-icon-top-right"
            @click="exportExcel"
            >导出数据</el-button
          >

          <!-- 导出 Excel end -->

          <!-- 新增 start -->
          <!-- <el-button type="primary" icon="el-icon-circle-plus-outline" @click="onAdd">新增</el-button> -->
          <!-- 新增 end -->
        </div>
        <!-- 操作按钮 end -->
      </el-header>
      <!-- 显示表格 start -->
      <div v-show="showTable">
        <el-main>
          <!-- 表格 start -->
          <el-table
            class="table"
            :data="tableData"
            style="width: 100%"
            border
            v-loading="tableLoading"
            :default-sort="{ prop: 'totalTime', order: 'descending' }"
            @sort-change="onSortChange"
            :header-cell-style="{
              background: '#f2f2f2',
              color: '#606266',
              height: '38px',
              padding: '0px'
            }"
            :cell-style="{ height: '38px', padding: '0' }"
          >
            <template v-for="(value, key) in tableFields">
              <el-table-column
                :key="key"
                :prop="value"
                :label="key"
                min-width="130"
                :fixed="activeFixed(value)"
              >
                <template slot-scope="scope">
                  <div>
                    {{ tableContentValue(scope.row, value) }}
                  </div>
                </template>
              </el-table-column>
            </template>
            <!-- <el-table-column label="操作" min-width="130" fixed="right">
              <template slot-scope="scope">
                <el-button @click.native.prevent="editRow(scope.$index, scope.row)" type="text" size="small">
                  编辑
                </el-button>
              </template>
            </el-table-column> -->
          </el-table>
          <!-- 表格 end -->

          <!-- 导出的表格 start -->
          <!-- 此表格始终保持隐藏 -->
          <el-table
            id="outTable"
            class="table"
            :data="exportTableData"
            style="width: 100%; display: none"
            border
          >
            <el-table-column
              v-for="(value, key) in tableFields"
              :key="key"
              :prop="value"
              :label="key"
              min-width="130"
              align="center"
            >
              <template slot-scope="scope">
                <div>
                  {{ tableContentValue(scope.row, value) }}
                </div>
              </template>
            </el-table-column>
          </el-table>
          <!-- 导出的表格 end -->
        </el-main>
        <el-footer>
          <!-- 分页 start -->
          <div class="block">
            <el-pagination
              background
              layout="prev, pager, next, total, sizes, jumper"
              :total="total"
              :current-page="currentPage"
              :page-sizes="[10, 20, 30, 40, 50, 60, 70, 80, 90]"
              :page-size="pageSize"
              @current-change="onCurrentChange"
              @size-change="onSizeChange"
            >
            </el-pagination>
          </div>
          <!-- 分页 end -->
        </el-footer>
      </div>
      <!-- 显示表格 end -->

      <!-- 显示柱状图 start -->
      <div v-show="!showTable" class="my-echarts">
        <div id="myChart"></div>
      </div>
      <!-- 显示柱状图 end -->
      <div></div>
    </el-container>
  </div>
</template>

<script>
// import JsonExcel from 'vue-json-excel'
import FileSaver from 'file-saver'
import XLSX from 'xlsx'

import { formatDateTime, getYesterday } from '../utils/utils.js'
export default {
  name: 'ScenicProfitStatistic',
  data() {
    /**
     * 公共验证函数(验证数字值)
     * @params message - 提示信息
     */
    function checkNumberMethod(name = '') {
      // 返回验证函数
      return (rule, value, callback) => {
        if (!value) {
          // 如果值为空，可以回调
          callback()
        }

        if (isNaN(value) || value < 0) {
          callback(new Error(name + '必须为正整数'))
        } else {
          callback()
        }
      }
    }

    /**
     * 公共验证函数(验证必填数字值)
     * @params message - 提示信息
     */
    function checkRequireNumberMethod(name = '') {
      // 返回验证函数
      return (rule, value, callback) => {
        if (!value) {
          return callback(new Error(name + '不能为空'))
        }

        if (isNaN(value) || value < 0) {
          callback(new Error(name + '必须为正整数'))
        } else {
          callback()
        }
      }
    }

    /**
     * 公共验证函数(验证必填值)
     * @params message - 提示信息
     */
    function checkRequireMethod(name = '') {
      // 返回验证函数
      return (rule, value, callback) => {
        if (!value) {
          return callback(new Error(name + '不能为空'))
        } else {
          callback()
        }
      }
    }

    return {
      showTable: true, // 是否显示表格
      feeInput: '', // 请输入手续费
      taxInput: '', // 请输入税点
      searchFeeInput: '', // 搜索的手续费
      searchTaxInput: '', // 搜索的税点
      tableFields: {
        //导出Excel表格的表头设置
		'景区名称': 'scenicSpotName',
		'总订单数':'allOrder',
		'寻宝订单数':'huntOrder',
		'抽奖订单数':'lotteryOrder',
		'寻宝订单占比':'huntProportion',
		'抽奖订单占比':'lotteryProportion',
		'客单价':'perCustomerTransaction',
		'寻宝客单价':'huntPerCustomerTransaction',
		'抽奖客单价':'lotteryPerCustomerTransaction',
      },
      tableData: [], // 表格数据

      searchTableFields: {
       '景区名称': 'scenicSpotName',
       '总订单数':'allOrder',
       '寻宝订单数':'huntOrder',
       '抽奖订单数':'lotteryOrder',
       '寻宝订单占比':'huntProportion',
       '抽奖订单占比':'lotteryProportion',
       '客单价':'perCustomerTransaction',
	   '寻宝客单价':'huntPerCustomerTransaction',
       '抽奖客单价':'lotteryPerCustomerTransaction',
      }, // 搜索后字段数据

      tableLoading: false,

      searchCompanyOptions: [], // 公司选项
      searchScenicOptions: [], // 景区选项
	  
	  huntSwitchOptions:[
		  {
		    value: '0',
		    label: '未开启寻宝活动'
		  },
		  {
		    value: '1',
		    label: '开启寻宝活动'
		  }
	  ],
      dateStateOptions: [
        {
          value: '0',
          label: '按日查询'
        },
        {
          value: '1',
          label: '按月查询'
        },
        {
          value: '2',
          label: '按年查询'
        }
      ], // 日期统计方式选项
      typeOptions: [
        {
          value: '1',
          label: '按景区统计'
        },
        {
          value: '2',
          label: '按机器人统计'
        }
      ], // 统计方式选项
      statusOptions: [
        {
          value: '1',
          label: '已运营景区'
        },
        {
          value: '2',
          label: '测试景区'
        },
        {
          value: '3',
          label: '预运营景区'
        }
      ], // 运营状态选项
      timeTypeOptions: [
        {
          value: '1',
          label: '运营时长'
        },
        {
          value: '2',
          label: '平均单台运营时长'
        }
      ], // 时长类型选项

      exportTableData: [], // 导出的全部表格数据
      downloadLoading: false, // 导出excel按钮的加载动画

      currentPage: 1, // 当前页数,
      pageSize: 10, // 每页显示条数

      dialogVisible: false, // 是否显示编辑dialog对话框

      dialogState: '', // dialog是新增还是编辑表单，新增为'add'，编辑为'edit'

      dateType: 'daterange', // 日期类型（月，日，年）
      valueFormat: 'yyyy-MM-dd', // 日期格式

      searchValidateForm: {
        companyId: '', // 公司id
        spotId: '', // 景区id
		huntSwitch:'',//是否参与寻宝
        selectDateState: '0', // 日期统计方式（日，月，年）
        startEndDate: [getYesterday(), getYesterday()], // 起止日期
      }, // 搜索表单验证对象
      searchVF: {
        companyId: '', // 公司id
        spotId: '', // 景区id
        huntSwitch:'',//是否参与寻宝
        selectDateState: '0', // 日期统计方式（日，月，年）
        startEndDate: [getYesterday(), getYesterday()], // 起止日期
      }, // 提交表单验证对象
      searchVFEcharts: {
       companyId: '', // 公司id
       spotId: '', // 景区id
       huntSwitch:'',//是否参与寻宝
       selectDateState: '0', // 日期统计方式（日，月，年）
       startEndDate: [getYesterday(), getYesterday()], // 起止日期
      }, // 统计报表表单验证对象
      validateForm: {}, // 表单验证对象

      companys: [], // 公司列表
      companyState: '', // 当前请选择公司

      scenics: [], // 景区列表
      scenicState: '', // 当前请选择景区

      timeChartData: [], // 多时间多景区列表数据

      total: 0, // 总条数
      typeTotalTime: 0, // 总运营时长

      order: 'totalTime desc', // 表格排序
      echartsOrder: null, // echarts图表排序

      myChart: null, // 柱状图echarts实例
      option: {}, // 柱状图optino

      // disabledStatGrossProfit: false, // 是否禁用统计报表功能
      disabledTimeType: false, // 是否禁用时长类型选择框功能
      disabledRobotCode: true, // 是否禁用输入机器人编号功能

      // 搜索表单规则
      searchValidateFormRules: {
        // 运营时长
        totalTime: [
          {
            validator: checkNumberMethod('运营时长'),
            required: true,
            trigger: ['blur', 'change']
          }
        ]
      },

      // 提交表单验证
      validateFormRules: {}
    }
  },
  created() {
    // 如果当前环境是开发环境才调用登录方法
    if (process.env.NODE_ENV == 'development') {
      this.login()
    }    

    this.getValidateForm()
    this.initTableData()
    // this.initEchartsData()
    this.getCompanyList()
    this.getScenicList()
    // this.getTimeChart()
  },
  mounted() {
    // this.drawEcharts()
  },
  methods: {
    // 获取表单验证对象
    getValidateForm() {
      var validateForm = {}
      for (var item in this.tableFields) {
        validateForm[this.tableFields[item]] = ''
      }
      this.validateForm = validateForm
      console.log(this.validateForm)
    },

    // 登录(必须)
    async login() {
      const { data: res } = await this.$http.post(
        '/loginSystem?username=houjingchen&password=houjingchen'
      )
    },

    /**
     * 初始化表格数据
     * @params - isPage - 是否分页
     */
    async initTableData(isPage = false) {
      var companyId = this.searchVF.companyId
      if (companyId == '' || !companyId) {
        companyId = ''
      }

      var spotId = this.searchVF.spotId
      if (spotId == '' || !spotId) {
        spotId = ''
      }

	  var huntSwitch = this.searchVF.huntSwitch
	  if (huntSwitch == '' || !huntSwitch) {
	    huntSwitch = ''
	  }

      var selectDateState = this.searchVF.selectDateState
      if (selectDateState == '' || !selectDateState) {
        selectDateState = null
      }

      var startEndDate = this.searchVF.startEndDate
      var startDate, endDate
      if (startEndDate == '' || !startEndDate) {
        // 因为数据量太大，查的太慢，所以默认传入当前月
        // startDate = formatMonth()
        // endDate = formatMonth()
        startDate = null
        endDate = null
      } else {
        startDate = formatDateTime(
          this.searchVF.startEndDate[0],
          this.searchVF.selectDateState
        )
        endDate = formatDateTime(
          this.searchVF.startEndDate[1],
          this.searchVF.selectDateState
        )
      }

	  console.log(startDate,endDate)
      this.tableLoading = true
	  const { data: res } = await this.$http({
	  	method:"post",
	  	url:"/system/broadcastHunt/getTreasureHuntDetail",
	  	data:{
			companyId:companyId,
			orderScenicSpotId:spotId,
			huntSwitch:huntSwitch,
			dateType:selectDateState, // 日期类型
			orderStartTime:startDate,
			orderEndTime:endDate,
			pageNum:this.currentPage, // 页码
			pageSize:this.pageSize // 每页条数
		},
	  	headers: {'Content-Type': 'application/json;charset=UTF-8'}
	  })
      if (res.state == '200') {
        this.tableData = res.data.list
        // var type = JSON.parse(res.type)
        this.total = res.data.total
        this.tableLoading = false
      }

      return res
    },


    /**
     * 根据景区ID获取公司列表（搜索时公司列表）
     */
    async getCompanyList(spotId) {
      if (!spotId) {
        spotId = null
      }
      const { data: res } = await this.$http.get(
        '/system/robot_time_statistics/companyList',
        {
          params: {
            spotId: spotId
          }
        }
      )
      if (res.state == '200') {
        this.searchCompanyOptions = res.data
        return res
      } else {
        return
      }
    },

    /**
     * 根据公司ID获取景区列表（搜索时景区列表）
     */
    async getScenicList(companyId) {
      if (!companyId) {
        companyId = null
      }
      const { data: res } = await this.$http.get(
        '/system/robot_time_statistics/spotList',
        {
          params: {
            companyId: companyId
          }
        }
      )
      if (res.state == '200') {
        this.searchScenicOptions = res.data
        return res
      } else {
        return
      }
    },

    /**
     * 导出 Excel 表格的全部数据
     */
    async getExportTableData() {
      var companyId = this.searchValidateForm.companyId
      if (companyId == '' || !companyId) {
        companyId = ''
      }

      var spotId = this.searchValidateForm.spotId
      if (spotId == '' || !spotId) {
        spotId = ''
      }
	  var huntSwitch = this.searchValidateForm.huntSwitch
	  if (huntSwitch == '' || !huntSwitch) {
		 huntSwitch = ''
	  }

      var selectDateState = this.searchValidateForm.selectDateState
      if (selectDateState == '' || !selectDateState) {
        selectDateState = ''
      }

      var startEndDate = this.searchValidateForm.startEndDate
      var startDate, endDate
      if (startEndDate == '' || !startEndDate) {
        // 因为数据量太大，查的太慢，所以默认传入当前月
        // startDate = formatMonth()
        // endDate = formatMonth()
        startDate = ''
        endDate = ''
      } else {
        startDate = formatDateTime(
          this.searchValidateForm.startEndDate[0],
          this.searchValidateForm.selectDateState
        )
        endDate = formatDateTime(
          this.searchValidateForm.startEndDate[1],
          this.searchValidateForm.selectDateState
        )
      }
	  let formData = new FormData();
	  formData.append("companyId", companyId);
	  formData.append("orderScenicSpotId", spotId);
	  formData.append("huntSwitch", huntSwitch);
	  formData.append("dateType", selectDateState);
	  formData.append("orderStartTime", startDate);
	  formData.append("orderEndTime", endDate);
	  const params = new URLSearchParams(formData)
	  const { data: res } = await this.$http.get(
	    `/system/broadcastHunt/exportTreasureHuntDetail?${params.toString()}`,{
			responseType: 'arraybuffer'
		}
	  )
	  
	  
	  if (res) {
	  	const url = window.URL.createObjectURL(
	  		new Blob([res], {
	  			type: 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet'
	  		})
	  	)
	  	const a = document.createElement('a')
	  	// console.log(url)
	  	a.href = url
	  	a.download = '寻宝详情列表'
	  	a.click()
	  	// this.$message({
	  	// 	message: '导出成功',
	  	// 	type: 'success'
	  	// })
	  } else {
	  	// this.$message({
	  	// 	message: '导入失败',
	  	// 	type: 'error'
	  	// })
	  }
      if (res.state == '200') {
        this.exportTableData = res.data
      } else {
        return
      }
    },

    // 日期选项值发生变化时触发
    onDateChange() {
      // 将对象转换成数组
      var arr = []
      for (let i in this.searchTableFields) {
        let o = {}
        o[i] = this.searchTableFields[i] //即添加了key值也赋了value值 o[i] 相当于o.name 此时i为变量
        arr.push(o)
      }
      if (this.searchValidateForm.selectDateState == '0') {
        // 按日查询
        this.dateType = 'daterange'
      } else if (this.searchValidateForm.selectDateState == '1') {
        // 按月查询
        this.dateType = 'monthrange'

      } else if (this.searchValidateForm.selectDateState == '2') {
        // 按年查询
        this.dateType = 'yearrange'
      }

      // 将数组转换成对象
      var result = {}
      arr.forEach((item) => {
        result[Object.keys(item)[0]] = Object.values(item)[0]
      })
      console.log(result);
      this.searchTableFields = result
    },

    // 表格排序触发
    onSortChange({ column, prop, order }) {
      console.log(column, prop, order)
      if (order == 'ascending') {
        order = 'asc'
      } else if (order == 'descending') {
        order = 'desc'
      }

      if (!order) {
        this.order = null
      } else {
        this.order = `${prop} ${order}`
      }
      this.initTableData()
    },

    /**
     * 查询公司名称获取列表数据
     */
    async getTableData() {
      var startEndDateValidateFormList = []
      var startEndDateVFList = []
	console.log('调用接口1')
      startEndDateValidateFormList = [
        formatDateTime(
          this.searchValidateForm.startEndDate[0],
          this.searchValidateForm.selectDateState
        ),
        formatDateTime(
          this.searchValidateForm.startEndDate[1],
          this.searchValidateForm.selectDateState
        )
      ]
	console.log('调用接口2')
      startEndDateVFList = [
        formatDateTime(
          this.searchVF.startEndDate[0],
          this.searchVF.selectDateState
        ),
        formatDateTime(
          this.searchVF.startEndDate[1],
          this.searchVF.selectDateState
        )
      ]
	console.log('调用接口3')
      // 如果输入框中的内容等于上次搜索的内容，则直接返回
  //     if (
  //       this.searchValidateForm.companyId == this.searchVF.companyId &&
  //       this.searchValidateForm.spotId == this.searchVF.spotId &&
